
@charset "UTF-8";
body {
  padding: 0;
  margin: 0;
  font-family: 'PT Sans Caption', sans-serif;
  background: #000000; }

a {
  text-decoration: none; }

p {
  color: #ffffff; }

header {
  width: 1280px;
  margin: 0 auto; }
  header .top-header {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    margin-top: 30px; }
    header .top-header .logo {
