      margin-bottom: 207px;
      margin-right: 30px;
      position: relative; }
      main .promo .promo__left-block h3 span {
        color: #FF4900; }
    main .promo .promo__left-block h3:after {
      content: "";
      display: block;
      background: #FF4900;
      width: 5px;
      height: 265px;
      position: absolute;
      top: 10px;
