main .price {
  width: 1180px;
  margin: 0 auto;
  height: 100vh;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center; }
  main .price h3 {
    color: #ffffff;
    text-align: center;
    text-transform: uppercase;
    font-size: 48px;
    font-family: 'Montserrat', sans-serif;
    position: relative; }
    main .price h3 span {
      color: #FF4900; }
  main .price .price__main {
    display: flex;
    justify-content: center;
    align-items: center; }
    main .price .price__main .left {
      width: 40%;
      display: flex;
      flex-direction: column;
      position: relative; }
      main .price .price__main .left button {
        font-size: 36px;
        color: #ccc;
        width: 360px;
        height: 90px;
        margin: 10px;
        background: none;
        border: none;
        border-radius: 8px;
