  main .price .arr-down {
    position: absolute;
    bottom: 50px;
    right: 50%; }

main .contacts {
  width: 1180px;
  margin: 0 auto;
  height: 100vh;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center; }
  main .contacts .left {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 30px; }
    main .contacts .left h3 {
      color: #ffffff;
      text-align: right;
      text-transform: uppercase;
      font-size: 48px;
      font-family: 'Montserrat', sans-serif;
      margin: 0;
      margin-top: 40px;
      margin-bottom: 207px;
      margin-right: 30px;
      position: relative; }
      main .contacts .left h3 span {
        color: #FF4900; }

.slick-prev:before {
  content: url(/build/img/arr-left.png);
  position: absolute;
  left: -40px;
  top: -25px; }

.slick-next:before {
  content: url(/build/img/arr-right.png);
  position: absolute;
  left: 25px;
  top: -25px; }

/* Меню бургер */
.menu {
  position: relative;
  display: flex;
  justify-content: flex-end;
  width: 40%; }

.menu__links-item {
  color: #FF4900;
  font-size: 18px;
  text-transform: uppercase;
  text-decoration: none;
  margin-right: 30px;
  cursor: pointer; }

.menu__links-item:hover {
  color: #ffffff; }

.menu__links {
  position: absolute;
  display: none;
  top: 5px;
  right: 30px;
  left: 0;
  z-index: 1000; }

.menu.menu_state_open .menu__links {
  display: flex;
  flex-direction: row; }

.menu__icon {
  display: inline-block;
  width: 45px;
  height: 35px;
  position: relative;
  cursor: pointer; }

.menu__icon span {
  display: block;
  position: absolute;
  height: 6px;
  width: 100%;
  background: #FF4900;
  border-radius: 9px;
  opacity: 1;
  left: 0;
  transform: rotate(0deg);
  transition: .35s ease-in-out; }

.menu__icon span:nth-child(1) {
  top: 0px; }

.menu__icon span:nth-child(2),
.menu__icon span:nth-child(3) {
  top: 13px; }

.menu__icon span:nth-child(4) {
  top: 26px; }

.menu.menu_state_open .menu__icon span:nth-child(1) {
  top: 18px;
  width: 0%;
  left: 50%; }

.menu.menu_state_open .menu__icon span:nth-child(2) {
  transform: rotate(45deg); }

.menu.menu_state_open .menu__icon span:nth-child(3) {
  transform: rotate(-45deg); }

.menu.menu_state_open .menu__icon span:nth-child(4) {
  top: 18px;
  width: 0%;
  left: 50%; }
